<template>
	<div class="wrapper">
		<div class="back-wrap">
			<div class="row mt-login" style="display: flex; justify-content: center;">
				<div class="col-md-5">
					<div class="card card-login mb-4">
						<div class="card-body pd-login">
							<h3 class="card-title text-center">
								<img :src="require('@/assets/images/logo.png')" style="width: 50%; margin-bottom: 30px;">
							</h3>
							<div>
								<div class="form-group">
									<label class="font-weight-bold">Nama Lengkap</label>
									<input class="form-control form-control-lg" type="text" v-model="fullname" @keyup.enter="register"/>
								</div>
								<div class="form-group">
									<label class="font-weight-bold">Username</label>
									<input class="form-control form-control-lg" type="text" v-model="username" @keyup.enter="register"/>
								</div>
								<div class="form-group">
									<label class="font-weight-bold">Email</label>
									<input class="form-control form-control-lg" type="email" v-model="email" @keyup.enter="register"/>
								</div>
								<div class="form-group">
									<label class="font-weight-bold">Password</label>
									<input class="form-control form-control-lg" type="password" v-model="password" @keyup.enter="register"/>
								</div>
								<div class="form-group">
									<label class="font-weight-bold">Konfirmasi Password</label>
									<input class="form-control form-control-lg" type="password" v-model="confirm_password" @keyup.enter="register"/>
								</div>
								<div class="text-center mt-4">
									<button @click="register" class="btn btn-lg btn-primary btn-block">Register</button>
								</div>
								<p class="mt-3">Sudah Punya Akun ? <router-link to="/login">Login Disini</router-link></p>
							</div>
						</div>
					</div>	
				</div>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default{
		name : 'Login',
		data(){
			return{
				fullname : '',
				email : '',
				username : '',
				password : '',
				confirm_password: '',
			}
		},
		mounted(){
			if(this.$store.state.auth.authenticated){
				this.$router.push('/');
			}
		},
		methods : {
			register(){
				if(this.username == "" || this.password == "" || this.fullname == "" || this.confirm_password == ""){
					this.$swal('Oops', 'Semua isian harus diisi', 'error');
				}else{

					if(this.password != this.confirm_password){
						this.$swal('Oops', 'Password tidak sama', 'error');
					}else{
						this.$toast.open({
							message: 'Registrasi Berhasil!',
							type: 'info',
							position: 'top-right',
						});
						this.$router.push("/email");
					}
				}
			}
		}
	}
</script>

<style scoped="">
	body{
		overflow-x: hidden;
	}
	.btn{
		border:none;
	}
	.wrapper{
		overflow-x: hidden;
		min-height: 100vh;
		background: var(--main-color);
	}
	.back-wrap{
		overflow-x: hidden;
		position: fixed;
		background: var(--main-color);
		width: 100%;
		min-height: 100vh;
		overflow-y: scroll;
	}
	.mt-login{
		margin-top: 100px;
		background: transparent;
	}
	.pd-login{
		padding: 50px;
	}
	.card-login{
		box-shadow: 10px 10px 10px 10px rgba(0,0,0,.1);
	}
</style>